.tu-card {
  border-radius: 4px;
  overflow: hidden;
  color: #303133;
  transition: 0.3s ease;
  box-shadow: 3px 3px 6px #c8d0e7, -2px -2px 5px #ffffff; }
  .tu-card__header {
    display: flex;
    align-items: center;
    border-bottom: 1px solid #DCDFE6; }
    .tu-card__header-title {
      display: inline-block;
      flex: 1;
      padding: 12px 16px;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
      font-size: 14px;
      color: #9baacf;
      font-weight: 600; }
    .tu-card__header-extra {
      float: right;
      margin-left: auto;
      padding: 0 16px; }
  .tu-card__body {
    padding: 12px 16px;
    font-size: 14px;
    color: #9baacf; }
  .tu-card--small .tu-card__header .tu-card__header-title {
    padding: 8px 12px;
    font-size: 12px; }
  .tu-card--small .tu-card__header .tu-card__header-extra {
    padding: 0 12px; }
  .tu-card--small .tu-card__body {
    padding: 8px 12px;
    font-size: 12px; }
  .tu-card--large .tu-card__header .tu-card__header-title {
    padding: 16px 20px;
    font-size: 14px; }
  .tu-card--large .tu-card__header .tu-card__header-extra {
    padding: 0 20px; }
  .tu-card--large .tu-card__body {
    padding: 16px 20px;
    font-size: 14px; }
  .tu-card.tu-card-shadow--inset {
    box-shadow: inset 2px 2px 5px #c8d0e7, inset -2px -2px 5px #ffffff; }
